.Row{
    color: #fff;
    margin: 0 20px;
    .rowPosters{
        display: flex;
        scroll-behavior: smooth;
        overflow-x: scroll;
        overflow-y: hidden;
        &::-webkit-scrollbar{
            display: none;  
        }
        .rowPoster{
            max-height: 100px;
            object-fit: contain;
            margin-right: 15px;
            width: 100%;
            transition: transform .4s;
            &:hover{
                transform: scale(1.08);
            }
        }
        .rowPosterLarge{
            max-height: 250px;
        }
    }
}