.Banner{
    .bannerCover{
        height: 23rem;
        background: center center;
        background-size: cover;
        object-fit: cover;
        .bannerContent{
            padding-top: 70px;
            padding-left: 20px;
            height: 220px;
            overflow: hidden;
            .bannerTitle{   
                font-size: 3rem;
                font-weight: 900;
                padding-bottom: .3rem;
            }
            .bannerButton{
                cursor: pointer;
                color: #fff;
                outline: none;
                border: none;
                font-weight: 700;
                border-radius: 0.2vw;
                padding: 0.5rem 2rem;
                margin-right: 1rem;
                background-color: rgba(51, 51, 51, 0.5);
                transition: all .3s;
                &:hover{
                    color: #000;
                    background-color: #cdcbcb;
                }
            }
            .bannerDescription{
                line-height: 1.3;
                width: 45rem;
                padding-top: 1rem;
                font-size: .8rem;
                max-width: 360px;
                height: 88px;
            }
        }
        .fadeButton{
            height: 10rem;
            background-image: linear-gradient(
                180deg,
                transparent,
                #2525259c,
                #000
            );
        }
    }
}   