.NavBar{
    width: 100%;
    position: fixed;
    transition: all .3s ease-in;
    .nav_container{
        width: 100%;
        height: 3.2rem;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .navLogo{
            padding-bottom: 5px;
        }
        .navLogo,.navAvatar{
            margin: 5px;
        }
    }
    .nav_black{
        background-color: black;
        transition: all .5s ease-in;

    }
}